.pure-material-button-contained {
  box-sizing: border-box;
  min-width: 64px;
  height: 36px;
  vertical-align: middle;
  text-align: center;
  text-overflow: ellipsis;
  text-transform: uppercase;
  color: rgb(var(--pure-material-onprimary-rgb, 255, 255, 255) );
  background-color: var(--color-second-bg);
  cursor: pointer;
  border: none;
  border-radius: 4px;
  outline: none;
  padding: 0 16px;
  font-family: Neue Montreal, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 36px;
  transition: box-shadow .2s;
  display: inline-block;
  position: relative;
  overflow: hidden;
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, .2), 0 2px 2px rgba(0, 0, 0, .14), 0 1px 5px rgba(0, 0, 0, .12);
}

.pure-material-button-contained::-moz-focus-inner {
  border: none;
}

.pure-material-button-contained:before {
  content: "";
  background-color: rgb(var(--pure-material-onprimary-rgb, 255, 255, 255) );
  opacity: 0;
  transition: opacity .2s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.pure-material-button-contained:after {
  content: "";
  width: 32px;
  height: 32px;
  background-color: rgb(var(--pure-material-onprimary-rgb, 255, 255, 255) );
  opacity: 0;
  border-radius: 50%;
  padding: 50%;
  transition: opacity 1s, transform .5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.pure-material-button-contained:hover, .pure-material-button-contained:focus {
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, .2), 0 4px 5px rgba(0, 0, 0, .14), 0 1px 10px rgba(0, 0, 0, .12);
}

.pure-material-button-contained:hover:before {
  opacity: .08;
}

.pure-material-button-contained:focus:before {
  opacity: .24;
}

.pure-material-button-contained:hover:focus:before {
  opacity: .3;
}

.pure-material-button-contained:active {
  box-shadow: 0 5px 5px -3px rgba(0, 0, 0, .2), 0 8px 10px 1px rgba(0, 0, 0, .14), 0 3px 14px 2px rgba(0, 0, 0, .12);
}

.pure-material-button-contained:active:after {
  opacity: .32;
  transition: transform;
  transform: translate(-50%, -50%)scale(0);
}

.pure-material-button-contained:disabled {
  color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), .38);
  background-color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), .12);
  box-shadow: none;
  cursor: initial;
}

